@import url(https://fonts.googleapis.com/css2?family=Rubik:wght@300&display=swap);
:root {
    --primary: #88bdbc;
    --secondary: #254e58;
    --tertiary: #112d32;
    --quart: #3b3831;
    --quint: #776f63;
    --white: #ffffff;
    font-family: 'Rubik', sans-serif;
}

.navbar {
    background-color: #88bdbc;
    background-color: var(--primary);
    height: 80px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 1.2rem;
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    z-index: 1001;
}

.navbar-container {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
    width: 100vw;
}

.navbar-title {
    color: #254e58;
    color: var(--secondary);
    justify-self: flex-start;
    margin-left: 20px;
    cursor: pointer;
    text-decoration: none;
    font-size: 2rem;
    display: flex;
    align-items: center;
}

.nav-menu {
    display: grid;
    grid-template-columns: repeat(6, auto);
    grid-gap: 20px;
    list-style: none;
    text-align: center;
    width: 60vw;
    justify-content: flex-end;
    margin-right: 2rem;
}

.glasses {
    font-size: 5rem;
    color: #3b3831;
    color: var(--quart);
    -webkit-animation: bounce 1.5s ease-in-out 0s 2;
            animation: bounce 1.5s ease-in-out 0s 2;
}

.glasses:hover {
    -webkit-animation: bounce 1.5s ease-in-out infinite;
            animation: bounce 1.5s ease-in-out infinite;
}

@-webkit-keyframes bounce {
    0%{-webkit-transform: translateY(0);transform: translateY(0);}
    20%{-webkit-transform: translateY(-40px);transform: translateY(-40px);}
    25%{-webkit-transform: translateY(5px);transform: translateY(5px);}
    35%{-webkit-transform: translateY(0);transform: translateY(0);}
    60%{-webkit-transform: translateY(-5px);transform: translateY(-5px);}
    100%{-webkit-transform: translateY(-5px) rotate(360deg);transform: translateY(-5px) rotate(360deg);}
}

@keyframes bounce {
    0%{-webkit-transform: translateY(0);transform: translateY(0);}
    20%{-webkit-transform: translateY(-40px);transform: translateY(-40px);}
    25%{-webkit-transform: translateY(5px);transform: translateY(5px);}
    35%{-webkit-transform: translateY(0);transform: translateY(0);}
    60%{-webkit-transform: translateY(-5px);transform: translateY(-5px);}
    100%{-webkit-transform: translateY(-5px) rotate(360deg);transform: translateY(-5px) rotate(360deg);}
}

.bars {
    color: #88bdbc;
    color: var(--primary);
}

.nav-item {
    height: 80px;
}

.nav-link {
    color: #3b3831;
    color: var(--quart);
    display: flex;
    align-items: center;
    text-decoration: none;
    padding: 0.5rem 1rem;
    height: 100%;
}

.nav-link:hover {
    border-bottom: 6px solid #ffffff;
    border-bottom: 6px solid var(--white);
    opacity: 0.8;
    font-weight: 600;
    transition: all 0.2s ease-out;
}

.mobile-menu-icon {
    display: none;
}

@media only screen and (max-width: 960px) {

    .navbar {
        background: #254e58;
        background: var(--secondary);
    }

    .nav-menu {
        display: flex;
        flex-direction: column;
        width: 100%;
        height: 100vh;
        top: 80px;
        position: absolute;
        left:-100%;
        transition: all 0.5s ease;
    }

    .nav-menu.active {
        justify-content: flex-start;
        background: #88bdbcd3;
        left: 0;
        opacity: 1;
        transition: all 0.5s ease;
        z-index: 1;
    }

    .nav-link {
        text-align: center;
        padding-top: 3rem !important;
        width: 100%;
        opacity: 1;
        display: table;
    }

    .nav-link:hover {
        background-color: #3b3831;
        background-color: var(--quart);
        border-bottom: none;
        color: #ffffff;
        color: var(--white);
        border-radius: 0;
        opacity: 1;
    }

    .glasses {
        display: none;
    }

    .navbar-title {
        font-size: 1.5rem;
        position: absolute;
        color: #88bdbc;
        color: var(--primary);
        top: 0;
        left: 0;
        -webkit-transform: translate(25%, 50%);
                transform: translate(25%, 50%);
    }

    .mobile-menu-icon {
        display: block;
        position: absolute;
        top: 0;
        right: 0;
        -webkit-transform: translate(-100%, 60%);
                transform: translate(-100%, 60%);
        font-size: 1.8rem;
        cursor: pointer;
    }

    .quit {
        color: #88bdbc;
        color: var(--primary);
        font-size: 2rem;
    }
}

