:root {
    --primary: #88bdbc;
    --secondary: #254e58;
    --tertiary: #112d32;
    --quart: #3b3831;
    --quint: #aaa08e;
    --white: #ffffff;
    font-family: 'Rubik', sans-serif;
}

.contact {
    margin: 0;
    padding: 0;
    background: #112d32;
    background: var(--tertiary);
    height: 95vh;
    width: 100vw;
    position: absolute;
}

.contact-title {
    color: #88bdbc;
    color: var(--primary);
    font-size: 5rem;
    padding-top: 10vh !important;
    margin-left: 20vw !important;
    -webkit-animation: slideLeft 2s ease-in;
            animation: slideLeft 2s ease-in;
}

@-webkit-keyframes slideLeft {
    0%{ 
        opacity: 0.3;
        -webkit-transform: translateX(-500px);
                transform: translateX(-500px); 
    }
    90% {
        opacity: 1;
        -webkit-transform: translateX(0px);
                transform: translateX(0px);
    }
}

@keyframes slideLeft {
    0%{ 
        opacity: 0.3;
        -webkit-transform: translateX(-500px);
                transform: translateX(-500px); 
    }
    90% {
        opacity: 1;
        -webkit-transform: translateX(0px);
                transform: translateX(0px);
    }
}

.contact-container {
    display: flex;
    justify-content: center;
    margin-top: 20vh !important;
    align-items: flex-start;
    height: 75%;
}

.contact-icons {
    position: relative;
    display: flex;
    -webkit-transform-style: preserve-3d;
            transform-style: preserve-3d;
    -webkit-transform: rotate(-25deg) skew(25deg);
            transform: rotate(-25deg) skew(25deg);
}

.contact-icon {
    position: relative;
    list-style-type: none;
    width: 100px;
    height: 100px;
    margin: 0 30px !important;
}

.contact-icon:before {
    content: '';
    position: absolute;
    bottom: -30px;
    left: 0;
    width: 100%;
    height: 30px;
    background: #88bdbc;
    background: var(--primary);
    -webkit-transform-origin: top;
            transform-origin: top;
    -webkit-transform: skewX(-41deg);
            transform: skewX(-41deg);
}

.contact-icon:after {
    content: '';
    position: absolute;
    top: 0;
    left: -28px;
    width: 28px;
    height: 100%;
    background: #88bdbc;
    background: var(--primary);
    -webkit-transform-origin: right;
            transform-origin: right;
    -webkit-transform: skewY(-49deg);
            transform: skewY(-49deg);
}

.side {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #88bdbc;
    background: var(--primary);
    color: #112d32;
    color: var(--tertiary);
    font-size: 60px !important;
}

.contact-icon:hover .side {
    z-index: 1000;
    transition: 0.3s;
    color: #ffffff;
    color: var(--white);
    box-shadow: -1px 1px 1px rgba(0,0,0,0.5);
}

.contact-icon:hover .side:nth-child(1) {
    -webkit-transform: translate(0px, -0px);
            transform: translate(0px, -0px);
    opacity: 0.2;
}
.contact-icon:hover .side:nth-child(2) {
    -webkit-transform: translate(10px, -10px);
            transform: translate(10px, -10px);
    opacity: 0.4;
}
.contact-icon:hover .side:nth-child(3) {
    -webkit-transform: translate(20px, -20px);
            transform: translate(20px, -20px);
    opacity: 0.6;
}
.contact-icon:hover .side:nth-child(4) {
    -webkit-transform: translate(30px, -30px);
            transform: translate(30px, -30px);
    opacity: 0.8;
}
.contact-icon:hover .side:nth-child(5) {
    -webkit-transform: translate(40px, -40px);
            transform: translate(40px, -40px);
    opacity: 1;
}

.contact-icon:nth-child(1):hover .side {
    background: steelblue;
}

.contact-icon:nth-child(2):hover .side {
    background: royalblue;
}

.contact-icon:nth-child(3):hover .side {
    background: palevioletred;
}

.contact-icon:nth-child(4):hover .side {
    background: maroon;
}

.contact-icon:nth-child(5):hover .side {
    background: lime;
}

@media  screen and (max-width: 960px) {
    .contact-title {
        font-size: 4rem;
    }

    .contact-icon {
        width: 60px;
        height: 60px;
        margin: 0 10px !important;
    }

    .contact-icon:before {
        bottom: -10px;
        height: 10px;
    }

    .contact-icon:after {
        left: -9px;
        width: 9px;
    }

    .side {
        font-size: 30px !important;
    }
}
